@import url(https://fonts.googleapis.com/css?family=Abel);
.navbar {
  background-color: #1c1c1c; }
  @media screen and (max-width: 1296px) {
    .navbar {
      padding: 0 10px; } }
  .navbar__container {
    max-width: 1280px;
    padding: 20px 0;
    min-height: 120px; }
    @media screen and (max-width: 1024px) {
      .navbar__container {
        padding: 5px 0;
        min-height: 100%; } }
    .navbar__container a {
      height: 100%; }
  .navbar__img1 {
    width: 300px;
    height: 50px; }
    @media screen and (max-width: 1024px) {
      .navbar__img1 {
        max-width: 80%;
        display: block;
        height: auto; } }
  .navbar__img2 {
    width: 122px;
    height: 50px; }
    @media screen and (max-width: 1024px) {
      .navbar__img2 {
        max-width: 80%;
        display: block;
        height: 80%; } }
  .navbar__img1, .navbar__img2 {
    margin-top: 20px; }
    @media screen and (max-width: 1024px) {
