.hero-image {
  height: 600px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%, 50%; }
  @media screen and (max-width: 648px) {
    .hero-image {
      height: 100%; } }
  .hero-image__gradient {
    height: 100%;
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(39%, rgba(0, 0, 0, 0)), color-stop(41%, rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, 0.65)));
    background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0) 39%, rgba(0, 0, 0, 0) 41%, rgba(0, 0, 0, 0.65) 100%);
    background-image: linear-gradient(rgba(0, 0, 0, 0) 39%, rgba(0, 0, 0, 0) 41%, rgba(0, 0, 0, 0.65) 100%); }
  .hero-image__content {
    color: #fff; }
    .hero-image__content__text {
      max-width: 700px;
      padding-top: 275px; }
      @media screen and (max-width: 648px) {
        .hero-image__content__text {
          padding-left: 5px;
          padding-right: 5px;
          padding-top: 150px;
          padding-bottom: 30px; } }
      .hero-image__content__text h1 {
        font-size: 48px;
        font-weight: bold;
        padding: 32px 0; }
      .hero-image__content__text p {
        font-size: 22px;
        line-height: 26px; }
        @media screen and (max-width: 648px) {
          .hero-image__content__text p {
            text-align: justify; } }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  font-family: "Abel", sans-serif; }

.movies__form {
  background-color: #1c1c1c;
  padding: 25px 20px 0;
  height: 105px;
  font-size: 48px; }
  @media screen and (max-width: 648px) {
    .movies__form {
      height: 100%;
      padding: 10px 20px; } }
  .movies__form__content {
    margin: 0 auto;
    position: relative; }
    .movies__form__content i {
      color: white;
      font-size: 31px;
      position: absolute;
      left: 20px;
      z-index: 50; }
      @media screen and (max-width: 648px) {
        .movies__form__content i {
          left: 10px !important;
          font-size: 25px;
          top: 7px; } }

.movies__grid__heading {
  padding: 30px 0;
  font-size: 42px; }
  @media screen and (max-width: 648px) {
    .movies__grid__heading {
      padding: 15px; } }

@media screen and (max-width: 559px) {
  .movies__grid__container {
    justify-content: center; } }

.movies__search {
  border: none;
  height: 55px;
  border-radius: 40px;
  color: #fff;
  background-color: #353535;
  padding-left: 70px; }
  @media screen and (max-width: 648px) {
    .movies__search {
      height: 100%;
      padding: 0;
      padding-left: 50px;
      font-size: 30px; } }

.load-more-button {
  color: #fff;
  padding: 20px;
  margin: 60px 0;
  width: 100%;
  font-size: 42px;
  background-color: #16d47b; }
  .load-more-button:hover {
    background-color: #23ed8e; }

.movie {
  display: inline-block;
  max-width: 280px;
  max-height: 435px;
  margin-top: 20px; }
  @media screen and (max-width: 559px) {
    .movie {
      max-width: 100%;
      width: 90%;
      max-height: 100%; } }
  .movie img {
    height: 100%;
    width: 100%; }
    @media screen and (max-width: 559px) {
      .movie img {
        width: 100%;
        display: block;
        height: auto; } }

.movie__info__header {
  padding-top: 40px; }
  @media screen and (max-width: 840px) {
    .movie__info__header {
      height: 100%;
      padding-top: 0; } }
  .movie__info__header__content {
    font-family: "Abel", sans-serif;
    color: #fff;
    height: 520px;
    margin: 0 auto;
    margin-left: auto;
    margin-right: auto;
    background: rgba(0, 0, 0, 0.5); }
    .movie__info__header__content img {
      height: auto;
      max-width: 350px; }
      @media screen and (max-width: 840px) {
        .movie__info__header__content img {
          padding-top: 10px;
          max-width: 100%;
          display: block;
          height: auto; } }
    .movie__info__header__content div {
      padding: 15px 40px; }
      @media screen and (max-width: 840px) {
        .movie__info__header__content div {
          padding: 10px;
          text-align: justify; } }
      .movie__info__header__content div h1 {
        font-family: Arial, Helvetica, sans-serif;
        font-size: 43px; }
        @media screen and (max-width: 840px) {
          .movie__info__header__content div h1 {
            font-size: 30px;
            text-align: center; } }
      .movie__info__header__content div h3 {
        font-family: Arial, Helvetica, sans-serif;
        font-size: 16px;
        margin-top: 25px;
        font-weight: bold; }
      .movie__info__header__content div h4 {
        font-family: Arial, Helvetica, sans-serif;
        margin: 21px 0; }
      .movie__info__header__content div p {
        font-size: 18px;
        line-height: 26px;
        margin: 18px 0; }
    .movie__info__header__content ul {
      list-style: none;
      flex-wrap: wrap; }
      @media screen and (max-width: 840px) {
        .movie__info__header__content ul {
          justify-content: center; } }
      .movie__info__header__content ul li {
        margin-right: 20px;
        font-size: 18px; }
    @media screen and (max-width: 1024px) {
      .movie__info__header__content {
        max-width: 100%; }
        .movie__info__header__content img {
          height: auto; } }
    @media screen and (max-width: 840px) {
      .movie__info__header__content {
        flex-direction: column;
        align-items: center;
        height: 100%; } }

.director-heading {
  margin-top: 5px !important; }

.movie__rating {
  padding: 0 !important; }
  .movie__rating meter {
    width: 250px;
    height: 20px;
    margin-top: 20px;
    margin-right: 20px;
    -webkit-user-modify: read-only !important; }
    @media screen and (max-width: 840px) {
      .movie__rating meter {
        width: 100%; } }
    .movie__rating meter::-webkit-meter-bar {
      background: #fff;
      border: 0; }

.movie__infobar {
  background-color: #1c1c1c;
  padding: 25px 20px 0;
  height: 105px;
  font-size: 48px; }
  @media screen and (max-width: 840px) {
    .movie__infobar {
      height: 100%;
      padding: 5px; } }
  .movie__infobar__container {
    margin: 0 auto;
    color: #fff;
    font-size: 22px; }
    @media screen and (max-width: 840px) {
      .movie__infobar__container {
        font-size: 18px; } }
    @media screen and (max-width: 590px) {
      .movie__infobar__container {
        font-size: 18px;
        flex-direction: column;
        width: 70%;
        justify-content: baseline; } }
    .movie__infobar__container div {
      display: flex;
      align-items: center; }
    .movie__infobar__container i {
      padding-right: 10px;
      font-size: 50px; }
      @media screen and (max-width: 840px) {
        .movie__infobar__container i {
          font-size: 18px; } }

.actors h1 {
  font-size: 41px;
  padding: 30px; }
  @media screen and (max-width: 840px) {
    .actors h1 {
      padding: 15px; } }

.actors__container {
  display: grid;
  grid-template-rows: repeat(4, minmax(190px, 267px));
  grid-template-columns: repeat(4, 1fr);
  grid-auto-flow: row;
  grid-gap: 40px; }
  @media screen and (max-width: 1024px) {
    .actors__container {
      grid-template-columns: repeat(3, 1fr); } }
  @media screen and (max-width: 840px) {
    .actors__container {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 5px; } }
  @media screen and (max-width: 582px) {
    .actors__container {
      grid-template-columns: repeat(1, 1fr); } }

.spinner-backdrop {
  position: absolute;
  z-index: 100;
  background: rgba(0, 0, 0, 0.9);
  width: 100%;
  height: 100%; }

.spinner-on-load {
  z-index: 101; }

.actor {
  color: #fff; }
  .actor img {
    width: 190px;
    height: auto; }
    @media screen and (max-width: 840px) {
      .actor img {
        max-width: 100%;
        width: 55%; } }
  .actor__data {
    max-width: 300px;
    width: 60%;
    background-color: #353535; }
    @media screen and (max-width: 840px) {
      .actor__data {
        max-width: 100%;
        width: 100%; } }
    .actor__data__name {
      font-family: "Abel", sans-serif;
      font-size: 22px;
      margin: 10px 20px;
      font-weight: 300; }
    .actor__data__movie_character {
      font-family: "Abel", sans-serif;
      font-size: 18px;
      margin: 0 20px 10px; }

.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #3498db;
  width: 120px;
  height: 120px !important;
  -webkit-animation: spin 2s linear infinite;
  /* Safari */
  animation: spin 0.8s linear infinite; }

/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

